/**
 * Component Flyout Panel is defined as
 * `<eui-layout-v0-flyout-panel>`
 *
 * Imperatively create component
 * @example
 * let component = new FlyoutPanel();
 *
 * Declaratively create component
 * @example
 * <eui-layout-v0-flyout-panel></eui-layout-v0-flyout-panel>
 *
 * @extends {Component}
 */
import { TemplateComponent, definition } from '@eui/component';
import template from './flyoutPanel.html';
import style from './flyoutPanel.css';
import '@eui/base';

/**
 * @property {String} panelTitle - title of the flyout panel.
 * @property {Number} width - width of the flyout panel.
 * @property {String} position - which side of the viewport the panel should be positioned.
 * @property {String} show - prop that indicates visibility of the flyout panel
 */
@definition(`eui-layout-v${__VERSION__}-flyout-panel`, {
  style,
  template,
  props: {
    panelTitle: { attribute: true, type: String },
    width: { attribute: true, type: Number, default: 400 },
    position: { attribute: true, type: String, default: 'right' },
    show: { attribute: true, type: Boolean },
  },
})
export default class FlyoutPanel extends TemplateComponent {
  constructor() {
    super();
    // function bindings
    this._addEventListeners = this._addEventListeners.bind(this);
    this._addResizeEventListener = this._addResizeEventListener.bind(this);
    this._removeEventListeners = this._removeEventListeners.bind(this);
    this._removeResizeEventListener = this._removeResizeEventListener.bind(this);
    this._onFooterSlotChange = this._onFooterSlotChange.bind(this);
    this._handleResize = this._handleResize.bind(this);
    this._handleWindowResize = this._handleWindowResize.bind(this);
    this.hidePanel = this.hidePanel.bind(this);
    this.showPanel = this.showPanel.bind(this);

    // elements
    this.flyoutPanel = this.shadowRoot.querySelector('.flyout-panel');
    this.flyoutPanelTitle = this.shadowRoot.querySelector('.panel__header__left__title');
    this.closeButton = this.shadowRoot.querySelector('eui-v0-icon.close-panel');
    this.flyoutPanelFooter = this.shadowRoot.querySelector('.panel__footer');

    // slots
    this.flyoutPanelFooterSlot = this.shadowRoot.querySelector('slot[name="footer"]');
  }

  /**
   * Add the necessary event listeners by attaching them to the relevant DOM elements.
   *
   * @function _addEventListeners
   * @private
   */
  _addEventListeners() {
    this._onFooterSlotChange();
    this.flyoutPanelFooterSlot.addEventListener('slotchange', this._onFooterSlotChange, { passive: true });
    this.closeButton.addEventListener('click', () => { this.hidePanel(); }, { passive: true });
  }

  /**
   * Remove the necessary event listeners.
   *
   * @function _addEventListeners
   * @private
   */
  _removeEventListeners() {
    this.flyoutPanelFooterSlot.removeEventListener('slotchange', this._onFooterSlotChange, { passive: true });
    this.closeButton.removeEventListener('click', () => { this.hidePanel(); }, { passive: true });
    this._removeResizeEventListener();
  }

  /**
   * Add the resize event listener to the window object.
   * Also resizing the flyoutpanel before it gets displayed
   *
   * @function _addResizeEventListener
   * @private
   */
  _addResizeEventListener() {
    this._handleResize();
    window.addEventListener('resize', this, { passive: true, useCapture: true });
  }

  /**
   * Remove the resize event listener from the window object.
   *
   * @function _removeResizeEventListener
   * @private
   */
  _removeResizeEventListener() {
    window.removeEventListener('resize', this, { passive: true, useCapture: true });
  }

  /**
   * Determines if the close icon should be visible or not.
   *
   * @function _onFooterSlotChange
   * @private
   */
  _onFooterSlotChange() {
    if (!this.querySelector('[slot="footer"]') && this.flyoutPanelFooter) {
      this.classList.add('hide-footer');
      this.closeButton.hidden = false;
    } else {
      this.classList.remove('hide-footer');
      this.closeButton.hidden = true;
    }
  }

  /**
   * This method handles all the resize events and throttles them
   * by handling the resize every 200ms.
   *
   * @method _handleWindowResize
   * @private
   */
  _handleWindowResize() {
    let lastCallTime = 0;
    const delay = 200;
    const throttleHandler = () => {
      const currentCallTime = (new Date()).getTime();
      if (currentCallTime - lastCallTime < delay) {
        return;
      }
      lastCallTime = currentCallTime;
      this._handleResize();
    };
    throttleHandler();
  }

  /**
   * Resizes the flyout panel depending on the width prop and viewport width
   *
   * @method _handleResize
   * @private
   */
  _handleResize() {
    const { clientWidth } = document.documentElement;
    if (clientWidth > this.width) {
      this.style.setProperty('--width', `${this.width}px`);
      this.style.setProperty('--width-neg', `-${this.width}px`);
    } else {
      this.style.setProperty('--width', `${clientWidth}px`);
      this.style.setProperty('--width-neg', `-${clientWidth}px`);
    }
  }

  handleEvent(event) {
    if (event.type === 'resize') {
      this._handleWindowResize();
    }
  }

  /** hide the flyout panel
   * @method hidePanel
   *
   */
  hidePanel() {
    // this.classList.remove('show');
    this.show = false;
    this._removeResizeEventListener();
  }

  /** show the flyout panel
   * @method showPanel
   *
   */
  showPanel() {
    // this.classList.add('show');
    this.show = true;
    this._addResizeEventListener();
  }

  /**
   * Called once when the flyout-panel component connects to the DOM.
   * The panel is hidden by default, and event listeners added.
   *
   * @function didConnect
   */
  didConnect() {
    this._addEventListeners();
  }

  /**
   * Lifecycle callback executed when the component is first rendered to the DOM
   *
   * @method didRender
   * @private
   */
  didRender() {
    this.compatElementUpdated();
  }

  didUpgrade() {
    this.flyoutPanelTitle.innerText = this.panelTitle;
    this._setFlyoutVisibility();
    this._setFlyoutWidth();
    this._guardFlyoutPosition();
  }

  /**
   * Called each time a prop changes on the component
   *
   * @method didChangeProps
   * @param {Map} changedProps - Keys are the names of changed properties;
   * Values are the corresponding previous values.
   */
  didChangeProps(changedProps) {
    if (changedProps.has('show')) {
      this._setFlyoutVisibility();
    }

    if (changedProps.has('panelTitle')) {
      this.flyoutPanelTitle.innerText = this.panelTitle;
    }

    if (changedProps.has('width')) {
      this._setFlyoutWidth();
    }

    if (changedProps.has('position')) {
      this._guardFlyoutPosition();
    }
  }

  /**
   * Set the flyout width. There is a safeguard in place
   * to prevent the width of the flyout to be less than 320px.
   *
   * @function _setFlyoutWidth
   * @private
   */
  _setFlyoutWidth = () => {
    if (this.width < 320) {
      this.width = 320;
    }
    this.style.setProperty('--width', `${this.width}px`);
    this.style.setProperty('--width-neg', `-${this.width}px`);
  }

  /**
   * Hide and show the Flyout panel.
   *
   * @function _setFlyoutVisibility
   * @private
   */
  _setFlyoutVisibility = () => {
    if (this.show) {
      this.showPanel();
    } else {
      this.hidePanel();
    }
  }

  /**
   * Add a safeguard, 'left'/'right' are the only valid choices.
   *
   * @function _guardFlyoutPosition
   * @private
   */
  _guardFlyoutPosition = () => {
    if (this.position !== 'left' && this.position !== 'right') {
      this.position = 'right';
    }
  }

  /**
   * Called once when the flyout-panel component disconnects from the DOM.
   * All event listeners are removed
   *
   * @function didDisconnect
   */
  didDisconnect() {
    this._removeEventListeners();
  }
}

/**
 * Register the component as eui-layout-v0-flyout-panel.
 * Registration can be done at a later time and with a different name
 */
FlyoutPanel.register();
