/**
 * Component MultiPanelTile is defined as
 * `<eui-layout-v0-multi-panel-tile>`
 *
 * Imperatively create component
 * @example
 * let component = new MultiPanelTile();
 *
 * Declaratively create component
 * @example
 * <eui-layout-v0-multi-panel-tile></eui-layout-v0-multi-panel-tile>
 *
 * @extends {Component}
 */
import { TemplateComponent, definition } from '@eui/component';
import template from './multiPanelTile.html';
import style from './multiPanelTile.css';

/**
 * @property {Number} column - number of the start column, when configured in a grid. Default is 0.
 * @property {Number} columnSpan - number of columns to span, when configured in a grid.
 * @property {Boolean} fixed - sets tile with panels that cannot be closed, default is false.
 * @property {Boolean} maximizable allows the size of the tile to be toggled between minimised
 * and maximized, default is false.
 * @property {Boolean} maximize - sets the tile to maximized and take up the full app area.
 * @property {Number} minWidth - the minimum width of the main panel. Defaults to 400.
 * @property {Number} rowSpan - number of rows to span, when configured in a grid. Default is 1.
 * @property {String} subtitle - subtitle of the tile.
 * @property {String} tileTitle - title of the tile.
 */
@definition(`eui-layout-v${__VERSION__}-multi-panel-tile`, {
  style,
  template,
  props: {
    column: { attribute: true, type: Number, default: 0 },
    columnSpan: { attribute: true, type: Number, default: 1 },
    fixed: { attribute: true, type: Boolean },
    maximizable: { attribute: true, type: Boolean, default: false },
    maximize: { attribute: true, type: Boolean },
    minWidth: { attribute: true, type: Number, default: 400 },
    rowSpan: { attribute: true, type: Number, default: 1 },
    subtitle: { attribute: true, type: String },
    tileTitle: { attribute: true, type: String },
    margins: { attribute: true, type: String },
  },
})
export default class MultiPanelTile extends TemplateComponent {
  constructor() {
    super();
    this.rightPanelButtonArea = this.shadowRoot.querySelector('.eui__tile__panel__button__area--right');
    this.leftPanelButtonArea = this.shadowRoot.querySelector('.eui__tile__panel__button__area--left');
    this.mainPanel = this.shadowRoot.querySelector('.eui__tile');

    // add an event listener to react to maximize button being clicked.
    this.maximizeButton = this.root.querySelector('#maximize-button');
  }

  _addEventListeners = () => {
    window.addEventListener('resize', this, { passive: true });
    this.addEventListener('eui-tile-panel:hide', this, { passive: true });
    this.addEventListener('eui-tile-panel:show', this, { passive: true });
    this.addEventListener('eui-tile-panel:disconnect', this, { passive: true });
    this.maximizeButton.addEventListener('click', this, { passive: true });
  }

  handleEvent(event) {
    if (event.type === 'resize') {
      this._resizeTile();
    }
    if (event.type === 'eui-tile-panel:show') {
      event.stopPropagation();
      this._showPanel(event.detail);
    }
    if (event.type === 'eui-tile-panel:hide') {
      event.stopPropagation();
      this._hidePanel(event.detail);
    }
    if (event.type === 'eui-tile-panel:disconnect') {
      event.stopPropagation();
      this._disconnectPanel(event.detail);
    }
    if (event.type === 'click') {
      this.maximize = !this.maximize;
    }
  }

  /**
   * Handler for the event "eui-tile-panel:disconnect"
   * disconnect a panel based on it's id and the size
   *
   * @function _disconnectPanel
   * @param {Object} {id, side} - The id and side/slot of the panel that was disconnected
   * @private
   */
  _disconnectPanel = ({ id, side }) => {
    const buttonArea = this.shadowRoot.querySelector(`.eui__tile__panel__button__area--${side}`);
    buttonArea.removeChild(this.shadowRoot.querySelector(`#open-panel-button-tooltip-${id}`));
    this._setPanelButtonArea(buttonArea);
  }

  /**
   * Handler for the event "eui-tile-panel:show"
   * show a panel based on it's id.
   *
   * @function _showPanel
   * @param {Object} panelDetail -  { id, side, title, icon }
   * @private
   */
  _showPanel = ({ id, side, icon }) => {
    const button = this.shadowRoot.querySelector(`#open-panel-button-${id}`);
    if (button) {
      this._setButtonVisibility(button, true, icon, side);
    }
  }

  /**
   * Handler for the event "eui-tile-panel:hide"
   * hide a panel based on it's id
   *
   * @function _hidePanel
   * @param {Object} panelDetail -  { id, side, title, icon }
   * @private
   */
  _hidePanel = ({
    id,
    title,
    side,
    icon,
  }) => {
    this.mainPanel.style.display = null;
    const button = this.shadowRoot.querySelector(`#open-panel-button-${id}`);
    this.shadowRoot.querySelector(`div#${side}`).classList.remove('eui__tile__panel--fullwidth');
    if (button) {
      this._setButtonVisibility(button, false, icon, side);
    } else if (!button) {
      this._addPanelButton(id, title, icon, side);
    }
  }

  /**
   * set the visibility of a panel button.
   * The button's visibility is set to true (visible) when the panel is hidden.
   * User needs to see the button to open a closed/hidden panel.
   * The button's visibility is set to false (hidden) when the panel is visible.
   * User does not need to see the button.
   *
   * @function _setButtonVisibility
   * @param {Object} button - Button icon for the panel to be opened/closed.
   * @param {Boolean} hidden - hide(true)/show(false) the button.
   * @param {String} icon - name of the icon used for the panel button.
   * @param {String} side - 'left'/'right' panel area where the button resides.
   * @private
   */
  _setButtonVisibility = (button, hidden, icon, side) => {
    button.name = icon;
    button.hidden = hidden;
    this._setPanelButtonArea(this[`${side}PanelButtonArea`]);
    this._animateSide(side);
  }

  /**
   * When resizing close the left panels first, then the right.
   *
   * NOTE:
   * This function is called on each window resize event so it's good practice
   * to throttle any actions/calculataions being performed so as to not cause poor performance.
   *
   * @function _resizeTile
   * @private
   */
  _resizeTile = () => {
    if (!this.resizeTimeout) {
      this.resizeTimeout = setTimeout(() => {
        this.resizeTimeout = null;
        const remainingWidth = this._maxAllowedPanelWidth();
        const visiblePanels = [...this._getVisiblePanels('left'), ...this._getVisiblePanels('right')];
        if (remainingWidth <= 0) {
          if (visiblePanels.length === 1) {
            this._setSidePanelFullWidth(true, visiblePanels[0].slot);
            visiblePanels[0].fillWidth(true);
          } else if (visiblePanels.length > 1) {
            visiblePanels[0].show = false;
          }
        } else if (visiblePanels.length === 0) {
          this._setSidePanelFullWidth(false);
        } else if (visiblePanels.length >= 1) {
          visiblePanels[0].fillWidth(false);
          this._setSidePanelFullWidth(false);
        }
      }, 66);
    }
  }

  /**
   * Open a panel. Determines if a panel can be opened based on the view size and
   * the number of panels already opened.
   * This is determined by the min size of the main panel.
   * If (the current component size - main panel size > size of panel + main panel size),
   * then it can be opened.
   *
   * @function _openPanel
   * @private
   */
  _openPanel = (panelId) => {
    // panel to display
    const panel = this.querySelector(`#${panelId}`);
    // width of the panel to be displayed = panelWidth

    let panelDisplayed = false;

    while (!panelDisplayed) {
      // get the available width
      const availableWidth = this._maxAllowedPanelWidth();
      // can I display the new panel?
      const canDisplay = availableWidth >= panel.width;

      // if I can display the new panel, then just show it.
      if (canDisplay) {
        this._displayPanel(panel);
        panelDisplayed = true;
      } else if (!this._closePanel('left')) {
        if (!this._closePanel('right')) {
          this._displayPanelFullWidth(panel);
          panelDisplayed = true;
        }
      }
    }
  }

  /**
   * Display the panel
   *
   * @function _displayPanel
   * @private
   */
  _displayPanel = (panel) => {
    this.mainPanel.style.display = null;
    panel.show = true;
  }

  /**
   * Display the panel full width
   *
   * @funcion _displayPanelFullWidth
   * @private
   */
  _displayPanelFullWidth = (panel) => {
    panel.show = true;
    panel.fillWidth(true);
    this._setSidePanelFullWidth(true, panel.slot);
  }

  /**
   * Set a side panel to take up the full width of the main tile area.
   * This is used when a panel is opened which leaves no room for the main content.
   * When this happens the panel should be the only thing displayed.
   *
   * @function _setSidePanelFullWidth
   * @param {Boolean} fullwidth - true to set the side panel to full width,
   * false to set it back to normal use
   * @param {String} side - 'left'|'right', the name of the side to set.
   * @private
   */
  _setSidePanelFullWidth = (fullwidth, side) => {
    if (fullwidth) {
      this.mainPanel.style.display = 'none';
      const sideArea = this.shadowRoot.querySelector(`div#${side}`);
      sideArea.classList.add('eui__tile__panel--fullwidth');
      sideArea.style.width = null;
    } else {
      this.shadowRoot.querySelector('div#right').classList.remove('eui__tile__panel--fullwidth');
      this.shadowRoot.querySelector('div#left').classList.remove('eui__tile__panel--fullwidth');
      this.mainPanel.style.display = null;
    }
  }

  /**
   * Close a panel from either the left or the right of the tile
   *
   * @function _closePanel
   * @param {String} side - 'left'|'right'
   * @returns Boolean - true if a panel was closed, otherwise false.
   * @private
   */
  _closePanel = (side) => {
    try {
      this._getVisiblePanels(side)[0].show = false;
    } catch (error) {
      return false;
    }
    return true;
  }

  /**
   * Add buttons to open the panels. All panel buttons must have tooltips.
   * The tooltips are set from the panels title.
   *
   * @function _addPanelButton
   * @private
   */
  _addPanelButton = (id, title, icon, side) => {
    const tooltip = document.createElement('eui-base-v0-tooltip');
    tooltip.id = `open-panel-button-tooltip-${id}`;
    tooltip.message = title;
    tooltip.position = side === 'right' ? 'left' : 'right';

    const openButton = document.createElement('eui-v0-icon');
    openButton.id = `open-panel-button-${id}`;
    openButton.name = icon;
    openButton.addEventListener('click', () => {
      this._openPanel(id);
    });
    tooltip.appendChild(openButton);
      this[`${side}PanelButtonArea`].appendChild(tooltip);
      this._setPanelButtonArea(this[`${side}PanelButtonArea`]);
      this._animateSide(side);
    }

  /**
   * Hide the panel button area when all buttons have been removed/hidden.
   *
   * @function _setPanelButtonArea
   * @param panelButtonArea - The button area to hide
   * @private
   */
  _setPanelButtonArea = (panelButtonArea) => {
    const buttonCount = panelButtonArea.childElementCount;
    if (buttonCount === 0) {
      panelButtonArea.classList.add('eui__tile__panel__button__area--hidden');
    } else if (this._getNumberOfHiddenButtonsInPanel(panelButtonArea) === buttonCount) {
      panelButtonArea.classList.add('eui__tile__panel__button__area--hidden');
    } else {
      panelButtonArea.classList.remove('eui__tile__panel__button__area--hidden');
    }
  }

  /**
   * A utility function to get the number of hidden buttons in a button panel.
   *
   * @function _getNumberOfHiddenButtonsInPanel
   * @params {Object} buttonPanel - the panel caintaing the buttons
   * @private
   */
  _getNumberOfHiddenButtonsInPanel = (buttonPanel) => {
    const hiddenButtons = [...buttonPanel.childNodes].filter(tooltip => tooltip.querySelector('eui-v0-icon').hidden === true);
    return hiddenButtons.length;
  }

  /**
   * Return the number of pixels remaining in the layout.
   * Use this information to determine if the new panel can be
   * displayed, or another panel must be closed first.
   *
   * @function _maxAllowedPanelWidth
   * @private
   */
  _maxAllowedPanelWidth = () => {
    const leftPanels = this._getVisiblePanels('left');
    let leftWidth = 0;
    leftPanels.forEach((panel) => {
      leftWidth += panel.width;
    });

    // the width of the panel on the right
    const rightPanels = this._getVisiblePanels('right');
    let rightWidth = 0;
    rightPanels.forEach((panel) => {
      rightWidth += panel.width;
    });

    return this.offsetWidth - (this.minWidth + leftWidth + rightWidth);
  }

  /**
   * Animate a side panel.
   *
   * @function _animateSide
   * @property {String} side - left|right panel side to animate.
   * @private
   */
  _animateSide = (side) => {
    this._setSideWidth(side, `${this._totalVisiblePanelWidth(side)}px`);
    this.mainPanel.classList.remove('eui__tile--hidden');
  }

  /**
   * Get the total width of the panels on a side. The width of each panel is
   * obtained from it's width prop.
   *
   * @function _totalVisiblePanelWidth
   * @param {String} side - 'left'|'right'
   * @private
   */
  _totalVisiblePanelWidth = (side) => {
    const visiblePanels = this._getVisiblePanels(side);
    let width = 0;
    visiblePanels.forEach((panel) => {
      width += panel.offsetWidth;
    });
    return width;
  }

  /**
   * Set the width of the side area.
   *
   * @function _setSideWidth
   * @private
   */
  _setSideWidth = (side, width) => {
    const sideArea = this.shadowRoot.querySelector(`#${side}`);
    if ([...sideArea.classList].includes('eui__tile__panel--fullwidth') === false) {
      sideArea.style.width = width;
    }
  }

  /**
   * get all added tile-panels
   *
   * @function _getAllPanels
   * @private
   */
  _getAllPanels = () => [...this.querySelectorAll(`eui-layout-v${__VERSION__}-tile-panel`)];

  /**
   * Get a list of panels on the left/right of the multi-sliding-tile.
   *
   * @function _getPanels
   * @param {String} side - left|right
   * @private
   */
  _getPanels = side => this._getAllPanels().filter(panel => panel.slot === side);

  /**
   * Get a list of visible panels on the left/right of the multi-sliding-tile.
   *
   * @function _getVisiblePanels
   * @param {String} side - left|right
   * @private
   */
  _getVisiblePanels = side => this._getPanels(side).filter(panel => panel.show === true);

  /**
   * Maximize or minimize the tile.
   * @function _setTileMaximizedState
   * @private
   */
  _setTileMaximizedState = () => {
    if (this.maximize) {
      this.maximizeButton.name = 'minimize';
      this.bubble('eui-tile:maximize', { id: this.id });
    } else {
      this.maximizeButton.name = 'maximize';
      this.bubble('eui-tile:minimize');
    }
  }

  /**
   * Called once when the multi-panel-tile component connects to the DOM.
   * The panels are then observed for deletions.
   *
   * @function didConnect
   */
  didConnect() {
    this._addEventListeners();
  }

  /**
   * Called once when the multi-panel-tile component disconnects from the DOM.
   * All event listeners are removed
   *
   * @function didDisconnect
   */
  didDisconnect() {
    window.removeEventListener('resize', this);
    this.removeEventListener('eui-tile-panel:hide', this);
    this.removeEventListener('eui-tile-panel:show', this);
    this.removeEventListener('eui-tile-panel:disconnect', this);
    this.maximizeButton.removeEventListener('click', this);
  }

  /**
   * Called each time the component renders.
   */
  didRender() {
    this.compatElementUpdated();
  }

  didUpgrade() {
    if (this.tileTitle) {
      this.root.querySelector('#main-panel-title').innerText = this.tileTitle;
    }

    if (this.subtitle) {
      this.root.querySelector('.eui__tile__header__centre__subtitle').innerText = this.subtitle;
    }

    this._setTileMaximizedState();

    if (this.column) {
      this.style.setProperty('--column', this.column);
    }

    if (this.columnSpan) {
      this.style.setProperty('--column-span', this.columnSpan);
    }

    if (this.rowSpan) {
      this.style.setProperty('--row-span', this.rowSpan);
    }

    if (this.margins) {
      this.root.querySelector('.eui__tile__content').classList.add(`eui__tile__content--margins-${this.margins}`);
    }
  }

  /**
   * Called each time a prop changes on the component
   *
   * @method didChangeProps
   * @param {*} changedProps - Keys are the names of changed properties;
   * Values are the corresponding previous values.
   */
  didChangeProps(changedProps) {
    if (changedProps.has('tileTitle')) {
      this.root.querySelector('#main-panel-title').innerText = this.tileTitle;
    }

    if (changedProps.has('subtitle')) {
      this.root.querySelector('.eui__tile__header__centre__subtitle').innerText = this.subtitle;
    }

    if (changedProps.has('maximize')) {
      this._setTileMaximizedState();
    }

    if (changedProps.has('column')) {
      this.style.setProperty('--column', this.column);
    }

    if (changedProps.has('columnSpan')) {
      this.style.setProperty('--column-span', this.columnSpan);
    }

    if (changedProps.has('rowSpan')) {
      this.style.setProperty('--row-span', this.rowSpan);
    }

    if (changedProps.has('margins')) {
      if (changedProps.get('margins')) {
        this.root.querySelector('.eui__tile__content').classList.remove(`eui__tile__content--margins-${changedProps.get('margins')}`);
      }
      this.root.querySelector('.eui__tile__content').classList.add(`eui__tile__content--margins-${this.margins}`);
    }
  }
}

/**
 * Register the component as eui-layout-v0-multi-panel-tile.
 * Registration can be done at a later time and with a different name
 */
MultiPanelTile.register();
