/**
 * Component TilePanel is defined as
 * `<eui-layout-v0-tile-panel>`
 *
 * Imperatively create component
 * @example
 * let component = new TilePanel();
 *
 * Declaratively create component
 * @example
 * <eui-layout-v0-tile-panel></eui-layout-v0-tile-panel>
 *
 * @extends {Component}
 */
import { definition } from '@eui/component';
import Tile from '../tile/Tile';
import style from './tilePanel.css';

/**
 * @property {Boolean} show - false, hides the tile panel, true shows the tile panel.
 * @property {String} iconName - name of the icon used to represent the tile panel.
 * @property {Number} width - the width of the panel. Defaults to 300.
 * @property {event} eui-tile-panel:hide - dispatched when the panel is hidden.
 * @property {event} eui-tile-panel:show - dispatched when the panel is shown.
 */
@definition(`eui-layout-v${__VERSION__}-tile-panel`, {
  style,
  props: {
    show: { attribute: true, type: Boolean },
    iconName: { attribute: true, type: String, default: 'square-dashed' },
    maximizable: { attribute: true, type: Boolean },
    width: { attribute: true, type: Number, default: 300 },
  },
})
export default class TilePanel extends Tile {
  /**
   * Called when the component is upgraded.
   * @method didUpgrade
   */
  didUpgrade() {
    const closeIcon = document.createElement('eui-v0-icon');
    closeIcon.classList.add('close-button');
    closeIcon.slot = 'action';
    closeIcon.name = 'cross';

    // store a reference to tile parent. This is needed to inform the
    // parent that the tile was disconnected
    this.multiPanelTile = this.parentNode;

    // click on the close-button.
    closeIcon.addEventListener('click', () => {
      this.toggleTilePanel();
    });

    this.shadowRoot.querySelector('.tile__header__right').appendChild(closeIcon);

    const payload = {
      id: this.id,
      title: this.tileTitle,
      side: this.slot,
      icon: this.iconName,
    };

    if (!this.show) {
      this.fillWidth(false);
      this.bubble('eui-tile-panel:hide', payload);
    }

    super.didUpgrade();
  }

  /**
   * Toggle the visibility of the tile panel.
   *
   * @function toggleTilePanel
   */
  toggleTilePanel = () => {
    this.show = !this.show;
  };

  /**
   * Make the panel ignore it's actual width and go to fullwidth.
   * This is used when displaying the panel in a tile which it too
   * small to have both a panel and an the main tile area visible.
   *
   * @function fillWidth
   * @param {boolean} fill - true, sets the panel to display fullwidth,
   * false sets it to width prop.
   */
  fillWidth = (fill) => {
    if (fill) {
      this.style.setProperty('--width', '100%');
      this.style.flex = 'auto';
    } else {
      this.style.setProperty('--width', `${this.width}px`);
      this.style.flex = null;
    }
  }

  /**
   * component is connected. Set an id for the component if there is not one aleady set.
   */
  didConnect() {
    if (!this.id) {
      this.id = `panel-${Math.round(Math.random() * 10000000)}`;
    }
    super.didConnect();
  }

  /**
   * Called once when the tile-panel component disconnects from the DOM.
   * It bubbles an event telling it's parent that it is leaving home.
   *
   * @function didDisconnect
   */
  didDisconnect() {
    const isPossibleToBubble = typeof this.multiPanelTile.bubble === 'function';

    if (!isPossibleToBubble) {
      return;
    }

    const payload = {
      id: this.id,
      title: this.tileTitle,
      side: this.slot,
      icon: this.iconName,
    };
    this.multiPanelTile.bubble('eui-tile-panel:disconnect', payload);

    super.didDisconnect();
  }

  /**
   * Called each time a prop changes on the component
   *
   * @method didChangeProps
   * @param {Map} changedProps - Keys are the names of changed properties;
   * Values are the corresponding previous values.
   */
  didChangeProps(changedProps) {
    super.didChangeProps(changedProps);
    if (changedProps.has('show')) {
      const payload = {
        id: this.id,
        title: this.tileTitle,
        side: this.slot,
        icon: this.iconName,
      };
      if (this.show) {
        this.bubble('eui-tile-panel:show', payload);
      } else {
        this.fillWidth(false);
        this.bubble('eui-tile-panel:hide', payload);
      }
    }

    if (changedProps.has('width')) {
      this.style.setProperty('--width', `${this.width}px`);
    }
  }
}

/**
 * Register the component as eui-layout-v0-tile-panel.
 * Registration can be done at a later time and with a different name
 */
TilePanel.register();
